@use 'variables' as v;

@mixin phone {
    @media (min-width: 480px) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: 768px) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}
@mixin track {
    @content;
}
@mixin progress {
    @content;
}

@mixin thumb {
    @content;
}